.errorPageContainer {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    transition: all 0.6s;
    font-family: "Lato", sans-serif;
    color: #888;
    margin-top: 15%;
}

#mainContainer {
    width: 100%;
    height: 100vh;
    text-align: center;
}

.secondaryContainer {
    vertical-align: middle;

    h1 {
        font-size: 50px;
        display: inline-block;
        padding-right: 12px;
        animation: type 0.5s alternate infinite;
    }
}

@keyframes type {
    from {
        box-shadow: inset -3px 0px 0px #888;
    }
    to {
        box-shadow: inset -3px 0px 0px transparent;
    }
}

.goToHomeBtn {
    display: flex;
    justify-content: center;
}
